import { connect } from "react-redux";
import uuid from "react-uuid";

import { RootStateType, Messages } from "types";

import UserMessage from "./UserMessage";
import { filterMessages } from "components/Chat/utils";

interface OwnProps {
    id: string;
}

interface Props extends OwnProps {
    messages: Messages;
    thumbnail: string;
    name: { first: string; last: string };
}

const MessagesArea = (props: Props) => {
    const { messages, thumbnail, name } = props;
    return (
        <section className="chat--core" aria-label="messages">
            {messages.map(message => {
                return <UserMessage message={message} thumbnail={thumbnail} name={name} key={uuid()} />;
            })}
        </section>
    );
};

const mapStateToProps = (state: RootStateType, ownProps: OwnProps) => {
    return {
        messages: filterMessages(state.messages.messages, ownProps.id),
        thumbnail: state.users.activeUser.picture.thumbnail,
        name: state.users.activeUser.name,
    };
};

export default connect(mapStateToProps, {})(MessagesArea);
